cc.Class({
    extends: require("Bet"),
    updateBet(currentBetData) {
        this._super(currentBetData);
        if (!this.isCircular) this.blockCircularBet();
    },
    enableBetBtn() {
        this._enableBet = true;
        this._super();
        if (!this.isCircular) this.blockCircularBet();
    },
    disableBetBtn() {
        this._enableBet = false;
        this._super();
    },
    blockCircularBet() {
        const { steps, currentBetData } = this.node.gSlotDataStore.slotBetDataStore.data;
        const maxBet = Math.max(...Object.values(steps));
        const minBet = Math.min(...Object.values(steps));
        this.increaseButton.interactable = this._enableBet && (currentBetData !== maxBet);
        this.decreaseButton.interactable = this._enableBet && (currentBetData !== minBet);
    },

});
